.App {
  text-align: center;
}
.pageTitle{
  margin: 20px 0 0 0;
}
.movieSearch{
  margin: 50px 0;
}
.movieSearch .input-group{
  margin: 20px auto;
}
.movieBox{
  border-bottom: 1px solid #cccccc;
  padding-bottom:20px;
}
.movieTitle{
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.movieYear{
  font-weight: bold;
}
.movieType{
  text-transform: capitalize;
}
.moviePoster{
  margin: 20px 0 20px 0;
}
.moviePoster img{
  object-fit: contain;
  height: 250px;
  width: 200px;
}
@media only screen and (max-width: 980px) {
  .movieBox{
    margin: 0 0 20px 0;
  }
  .movieTitle{
    min-height: 50px;
    font-size: 20px;
  }
  .moviePoster{
    margin: 0px 0 10px 0;
  }
}